.header {
	justify-content: space-between;
	font-size: 12px;
	min-height: 150px;
	@include lg {
		min-height: 200px; }
	@include sm {
		padding-top: 40px; }
	&-wrapper {
		position: relative;
		background-color: $accent;
		background-repeat: no-repeat;
		background-size: cover;
		background-position: center top;
		min-height: 400px;
		@include sm {
			background-position: left top; }
		&:after {
			content: '';
			top: 0;
			left: 0;
			bottom: 0;
			right: 0;
			position: absolute;
			background-color: #4a5666;
			opacity: .5; }
		&:before {
			content: '';
			position: absolute;
			right: 0;
			bottom: 0;
			width: 570px;
			height: 570px;
			background-image: url(../img/bg-1.png);
			background-repeat: no-repeat;
			background-size: cover;
			background-position: center;
			@include sm {
				background: none; } } } }
.logo {
	display: flex;
	flex-wrap: wrap;
	flex-direction: column;
	justify-content: center;
	@include sm {
		margin: 0 auto;
		padding-left: 150px;
		margin-bottom: 40px;
		a {
			display: inline-block;
			width: 180px;
			height: 140px;
			background: url(../img/svgo/logo-tablet.svg) center no-repeat; }
		span {
			margin: 10px 0 0 0;
			text-align: center; } }
	@include xs {
		padding: 0;
		margin-bottom: 0; }
	img {
		max-width: 190px;
		@include sm {
			display: none; } }
	span {
		margin: 0 0 0 auto;
		color: #ffffff;
		font-family: rl; } }
nav {
	@include lg {
		order: 3;
		width: 100%; }
	ul {
		align-items: center;
		@include lg {
			margin: 0 auto; }
		@include xs {
			flex-direction: column;
			justify-content: center; }
		li {
			display: inline-block;
			margin: 0 15px;
			a {
				font-size: 14px;
				text-transform: uppercase;
				color: #fff;
				position: relative;
				border-bottom-width: 3px;
				border-bottom-style: solid;
				border-bottom-color: transparent;
				padding-bottom: 10px;
				transition: all 0.35s ease;
				@include xs {
					font-size: 22px; }
				&:hover {
					padding-bottom: 2px;
					border-bottom-color: rgb(40, 167, 228); } } } } }

.m-active {
	padding-bottom: 2px;
	border-bottom-color: rgb(40, 167, 228); }
// MOBILE MENU
.mobile__nav {
	@include xs {
		display: block;
		position: fixed;
		top: 0;
		left: 0; }
	display: none;
	width: 70px;
	height: 70px;
	background-color: #dfdfdf;
	position: relative;
	z-index: 10;
	span {
		position: absolute;
		width: 20px;
		height: 3px;
		display: block;
		background-color: $accent;
		top: 50%;
		left: 60%;
		transform: translate(-50%,-50%);
		transition: .5s;
		transition-delay: .5s;
		&:before {
			content: '';
			position: absolute;
			width: 30px;
			height: 100%;
			background-color: $accent;
			display: block;
			top: -8px;
			right: 0; }
		&:after {
			content: '';
			position: absolute;
			width: 25px;
			height: 100%;
			background-color: $accent;
			display: block;
			top: 8px;
			right: 0; } } }
.feedback {
	display: flex;
	align-items: center;
	@include sm {
		flex-direction: column;
		align-items: center; }
	@include xs {
		display: none; }
	a:first-child {
		color: #ffffff;
		font-size: 16px;
		margin-right: 20px;
		@include sm {
			margin: 0	0	15px	0; }
		strong {
			font-size: 20px;
			font-weight: 400; } } }
.content {
	color: #ffffff;
	padding: 100px 0 150px 0;
	@include r(1300) {
		padding: 50px 5%; }
	@include lg {
		justify-content: center; }
	&-title {
		width: 35%;
		@include lg {
			width: 48%; }
		@include sm {
			width: 100%;
			text-align: center; }
		h1 {
			text-transform: uppercase;
			font-size: 48px;
			position: relative;
			padding-bottom: 20px;
			@include xs {
				font-size: 30px; }
			&::before {
				content: '';
				position: absolute;
				left: 0;
				bottom: 0;
				height: 3px;
				width: 20%;
				background-color: $line;
				@include sm {
					left: 40%; } } }
		p {
			margin: 20px 0;
			font-family: rl; }
		button {
			padding: 20px 30px;
			@include sm {
				display: none; }
			@include xs {
				display: block;
				margin: 40px auto; } }
		a {
			color: #ffffff;
			transition: color .25s ease;
			@include sm {
				display: none; }
			&:hover {
				color: $link; } } }
	&-list {
		min-width: 250px;
		margin: 16px 0 0 100px;
		@include lg {
			width: 48%;
			margin: 0;
			padding-top: 20px; }
		@include sm {
			margin: 40px 0 0 0;
			width: 100%;
			text-align: center; }
		button {
			display: none;
			@include sm {
				display: block;
				margin: 20px auto;
				padding: 18px 40px; }
			@include xs {
				display: none; } }
		a {
			display: none;
			@include xs {
				display: inline-block; } } }

	.list {
		&__item {
			@include sm {
				padding: 95px 0 0 0;
				text-align: center;
				width: 48%;
				display: inline-block;
				margin-bottom: 40px;
				h4 {
					font-size: 16px;
					margin-bottom: 10px; }
				p {
					font-size: 16px; }
				span {
					width: 84px;
					height: 84px;
					left: 50%;
					margin-left: -42px;
					background-size: 40px;
					border: 4px solid $icon; } }
			@include xs {
				width: 100%; } } } }

