.footer-wrapper {
	width: 100%;
	min-height: 200px;
	background-color: $foot; }
footer {
	padding: 50px 0;
	justify-content: space-between;
	@include r(1300) {
		padding: 50px 5%; }
	@include sm {
		padding-bottom: 20px; } }
.footer__logo {
	@include sm {
		margin: 0 auto;
		padding: 0;
		a {
			margin: 0 auto; } }

	img {
		max-width: 250px; }
	span {
		margin-left: 65px;
		font-size: 10px;
		opacity: .5;
		@include sm {
			margin-top: 30px;
			font-size: 12px; } } }
.footer__nav {
	width: 400px;
	justify-content: space-between;
	border-top: 4px solid rgba(#fff, .05);
	padding-top: 17px;
	@include md {
		flex-direction: column;
		width: 180px;
		border: none;
		padding: 0; }
	@include sm {
		flex-direction: row;
		width: 100%;
		min-height: 150px;
		border: none;
		border-bottom: 4px solid rgba(#fff, .05);
		text-align: center;
		margin-top: 20px; }
	@include xs {
		flex-direction: column;
		padding-bottom: 20px; }
	a {
		font-size: 14px;
		color: #f4f4f4;
		text-decoration: underline #f4f4f4;
		@include sm {
			flex: 1 2 30%; }
		@include xs {
			flex: none;
			margin-bottom: 10px; }
		&:hover {
			color: darken(#f4f4f4, 20%); } } }
.footer__contact {
	font-family: rl;
	border-left: 2px solid rgba(#fff, .05);
	padding-left: 20px;
	color: #f4f4f4;
	font-size: 18px;
	text-align: right;
	width: 200px;
	@include sm {
		border: none;
		padding: 20px 0 0 0;
		display: flex;
		width: 100%;
		justify-content: space-around; }
	@include xs {
		flex-direction: column;
		text-align: center; }
	p {
		margin-bottom: 10px;
		padding-right: 30px;
		position: relative;
		@include sm {
			padding: 0;
			padding-left: 30px; }
		@include xs {
			padding: 0; } }
	p:first-child,p:last-child {
		&:before {
			content: '';
			position: absolute;
			width: 15px;
			height: 15px;
			top: 5px;
			right: 0;
			background: url(../img/svgo/tel.svg) center no-repeat;
			background-size: 15px;
			@include sm {
				left: 0; }
			@include xs {
				display: none; } } }
	p:last-child {
		&:before {
			background: url(../img/svgo/mail.svg) center no-repeat; } } }

.root {
	width: 100%;
	padding: 25px;
	background-color: #474747;
	p {
		font-size: 12px;
		text-transform: uppercase;
		font-family: rl;
		color: #fff;
		opacity: .3;
		text-align: center; } }
