.menu {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  z-index: 1000;
  pointer-events: none;
  color: #fff;
  text-align: center;
}

.menu::before {
  content: '';
  position: absolute;
  width: 100%;
  height: 100%;
  background: rgba(34, 40, 47, 0.969);
  right: 0;
  transition: opacity 0.3s;
  pointer-events: none;
  opacity: 0;
  z-index: -1;
}

.menu-open::before {
  opacity: 1;
  pointer-events: auto;
  transition: all .20s ease;
}

.menu-open .menu_inner {
  overflow-x: auto;
}

.menu-open .menu_inner:before {
  content: '';
  position: absolute;
  top: 40px;
  left: 50%;
  margin-left: -90px;
  width: 180px;
  height: 140px;
  background: url(../img/svgo/logo-overlay.svg) center no-repeat;
}

.menu-open .menu_inner ul > li {
  pointer-events: auto;
  transform: translateY(0);
  opacity: 1;
}

.menu-open .menu_inner ul > li:nth-child(1) {
  transition: all .4s ease;
}

.menu-open .menu_inner ul > li:nth-child(2) {
  transition: all .6s ease;
}

.menu-open .menu_inner ul > li:nth-child(3) {
  transition: all .8s ease;
}

.menu-open .menu_inner ul > li:nth-child(4) {
  transition: all 1s ease;
}

.menu-open .close {
  opacity: .7;
  transition: all 0.35s ease;
  pointer-events: auto;
  font-size: 70px;
  font-family: rh;
}

.menu-open .menu_logo {
  transition: all 0.35s ease;
  opacity: .4;
  pointer-events: auto;
}

