.modal {
	position: relative;
	min-width: 300px;
	max-width: 440px;
	padding: 60px 40px;
	margin: 250px auto;
	background-color: #fff;
	color: #333;
	@include xs {
		margin: 140px auto;
		ma {} }
	&:before {
		content: '';
		position: absolute;
		top: -220px;
		left: 50%;
		transform: translateX(-50%);
		max-width: 200px;
		width: 100%;
		height: 180px;
		background-image: url(../img/svgo/logo-overlay.svg);
		background-repeat: no-repeat;
		background-position: center;
		background-size: 100%;
		@include xs {
			max-width: 100px;
			top: -160px; } }
	&-close {
		margin: 10px 10px 0 0;
		font-family: rh;
		font-size: 50px; } }

.mfp-move-from-top.mfp-ready.mfp-bg {
	background-color: #333;
	opacity: .97 !important;

	/* Сверху */ }
.mfp-move-from-top .mfp-content {
	vertical-align: top; }
.mfp-move-from-top .mfp-with-anim {
	opacity: 0;
	transition: all 0.2s;
	transform: translateY(-100px); }
.mfp-move-from-top.mfp-bg {
	opacity: 0;
	transition: all 0.2s; }
.mfp-move-from-top.mfp-ready .mfp-with-anim {
	opacity: 1;
	transform: translateY(0); }
.mfp-move-from-top.mfp-ready.mfp-bg {
	opacity: 0.8; }
.mfp-move-from-top.mfp-removing .mfp-with-anim {
	transform: translateY(-50px);
	opacity: 0; }
.mfp-move-from-top.mfp-removing .mfp-bg {
	opacity: 0; }

#text {
	font-size: 18px;
	color: #333;
	text-align: center;
	@include xs {
		padding: {
			left: 10px;
			right: 10px; } }
	h4 {
		text-transform: uppercase; }
	p {
		font-family: rt;
		margin-top: 20px; } }
#callback {
	padding: 60px;
	@include xs {
		padding: 60px 10px 40px 10px; }
	.list__item {
		color: #333;
		p {
			color: #333; } }
	form {
		input, textarea {
			&::placeholder {
				color: #333; } } } }
#thanks {
	padding: 50px 30px 40px 30px;
	p {
		text-align: center;
		margin-bottom: 30px;
		line-height: 1.7; } }
#slider {
	padding: 0;
	max-width: 800px;
	width: 100%;
	.modal-close {
		position: absolute;
		top: -60px;
		right: -20px;
		color: #fff;
		opacity: .6;
		&:hover {
			opacity: 1; } }
	.sl {
		min-height: 500px;
		width: 100%;
		background-repeat: no-repeat;
		background-size: cover;
		background-position: center;
		@include xs {
			min-height: 250px; } }
	.owl-nav {
		height: 0; }
	.popup__nav {
		display: inline-block;
		position: absolute;
		top: 50%;
		margin-top: -25px;
		width: 50px;
		height: 50px;
		background: url(../img/svgo/next.svg) center no-repeat;
		background-size: 45px;
		&-next {
			right: -70px;
			@include xs {
				right: 0; } }
		&-prev {
			left: -70px;
			transform: rotate(180deg);
			@include xs {
				left: 0; } } } }
.modal-video {
	max-width: 800px;
	padding: 0;
	.mfp-close {
		font-family: rh;
		font-size: 40px;
		top: -50px; } }
// MENU
.menu {
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	height: 100vh;
	z-index: 1000;
	pointer-events: none;
	color: #fff;
	text-align: center;
	&::before {
		content: '';
		position: absolute;
		width: 100%;
		height: 100%;
		background: rgba(34, 40, 47, 0.969);
		right: 0;
		transition: opacity 0.3s;
		pointer-events: none;
		opacity: 0;
		z-index: -1; }
	&-open {
		&::before {
			opacity: 1;
			pointer-events: auto;
			transition: all .20s ease; }
		.menu_inner {
			overflow-x: auto;
			&:before {
				content: '';
				position: absolute;
				top: 40px;
				left: 50%;
				margin-left: -90px;
				width: 180px;
				height: 140px;
				background: url(../img/svgo/logo-overlay.svg) center no-repeat; }
			ul > li {
				pointer-events: auto;
				transform: translateY(0);
				opacity: 1;
				&:nth-child(1) {
				  transition: all .4s ease; }
				&:nth-child(2) {
					transition: all .6s ease; }
				&:nth-child(3) {
					transition: all .8s ease; }
				&:nth-child(4) {
					transition: all 1s ease; } } }
		.close {
			opacity: .7;
			transition: all 0.35s ease;
			pointer-events: auto;
			font-size: 70px;
			font-family: rh; }
		.menu_logo {
			transition: all 0.35s ease;
			opacity: .4;
			pointer-events: auto; } }
	&_inner {
		display: flex;
		align-items: center;
		height: 100%;
		@include xs {
			padding-top: 39px; } }
	ul > li {
		opacity: 0;
		margin: 0 0 30px 0;
		transform: translateY(40px); }
	&_logo {
		margin-bottom: 30px;
		opacity: 0;
		pointer-events: none;
		img {
			width: 180px;
			height: 140px; } } }
.close {
	position: absolute;
	top: 40px;
	right: 20px;
	opacity: 0;
	cursor: pointer;
	z-index: 1100;
	pointer-events: none; }
